<template>
  <div class="content">
    <div class="logo">
      <img :src="imgSrc" alt="">
      <h2>{{ name }}</h2>
    </div>
    <button class="btn" @click="togo"><i class="el-icon-menu"></i></button>
    <ul class="menu" :class="{ 'active': status }" @click="togo">
      <li v-for="(item, index) in menusL" :key="index"><a :href="'#' + item.name">{{ item.title }}</a></li>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    imgSrc: {
      type: String,
      default: 'https://picsum.photos/id/15/800/600'
    },
    name: {
      type: String,
      default: '饭醉集团'
    },
    menusL: {
      type: Array,
      default: () => [
        {
          name: 'about',
          title: '关于'
        }, {
          name: 'contact',
          title: '联系我们'
        }
      ]
    }
  },
  data() {
    return {
      status: false
    }
  },
  methods: {
    togo() {
      this.status = !this.status
    }
  }
}
</script>

