<template>
  <div class="iconS">
    <i :class="[iconClass]" class="icon"></i>
  </div>
</template>

<script>
export default {
  props: {
    iconClass: {
      type: String,
      default: 'el-icon-info'
    },
    color: {
      type: String,
      default: '#3B82F6B0;'
    },
  }
}
</script>

<style scoped>
.iconS {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  position: relative;
  margin-right: 20px;
}

.icon {
  font-size: 25px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
</style>