<template>
  <div class="carousel-container" :style="{ height: height + 'px' }" ref="carousel" @mouseenter="stopAutoplay"
    @mouseleave="startAutoplayIfNeeded">
    <div class="carousel-wrapper" :style="{ transform: transformStyle, transition: transitionStyle }">
      <div class="carousel-slide" v-for="(item, index) in slides" :key="item.id || index"
        :style="{ width: slideWidth }">
        <img :src="item.image" :alt="item.alt || '轮播图'" class="carousel-image">
        <div v-if="item.title" class="carousel-caption">
          <h3>{{ item.title }}</h3>
          <p>{{ item.description }}</p>
        </div>
      </div>
    </div>

    <button class="carousel-control prev" @click="prevSlide" :disabled="isFirstSlide">
      <i class="el-icon-arrow-left"></i>
    </button>
    <button class="carousel-control next" @click="nextSlide" :disabled="isLastSlide">
      <i class="el-icon-arrow-right"></i>
    </button>

    <div class="carousel-indicators" v-if="indicators">
      <button v-for="(item, index) in slides" :key="item.id || index" :class="{ active: currentIndex === index }"
        @click="goToSlide(index)"></button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Carousel',
  props: {
    slides: {
      type: Array,
      required: true,
      default: () => []
    },
    autoplay: {
      type: Boolean,
      default: true
    },
    interval: {
      type: Number,
      default: 3000
    },
    transitionDuration: {
      type: Number,
      default: 500
    },
    indicators: {
      type: Boolean,
      default: true
    },
    height: {
      type: Number,
      default: 500
    }
  },
  data() {
    return {
      currentIndex: 0,
      slideWidth: '100%',
      autoplayTimer: null,
      userPaused: false
    }
  },
  computed: {
    transformStyle() {
      return `translateX(-${this.currentIndex * 100}%)`
    },
    transitionStyle() {
      return `transform ${this.transitionDuration}ms ease-in-out`
    },
    isFirstSlide() {
      return this.currentIndex === 0
    },
    isLastSlide() {
      return this.currentIndex === this.slides.length - 1
    }
  },
  watch: {
    slides: {
      handler() {
        this.resetCarousel()
      },
      deep: true
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.updateSlideWidth()
      window.addEventListener('resize', this.updateSlideWidth)

      if (this.autoplay) {
        this.startAutoplay()
      }
    })
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.updateSlideWidth)
    this.stopAutoplay()
  },
  methods: {
    updateSlideWidth() {
      if (this.$refs.carousel && this.slides.length > 0) {
        this.slideWidth = '100%'
      }
    },
    startAutoplay() {
      if (!this.autoplay || this.userPaused) return

      this.stopAutoplay()
      this.autoplayTimer = setInterval(() => {
        this.nextSlide()
      }, this.interval)
    },
    stopAutoplay() {
      if (this.autoplayTimer) {
        clearInterval(this.autoplayTimer)
        this.autoplayTimer = null
      }
    },
    startAutoplayIfNeeded() {
      if (this.autoplay && !this.userPaused) {
        this.startAutoplay()
      }
    },
    prevSlide() {
      if (this.isFirstSlide) return

      this.userPaused = true
      this.stopAutoplay()
      this.currentIndex--
    },
    nextSlide() {
      if (this.isLastSlide) {
        this.currentIndex = 0
      } else {
        this.currentIndex++
      }
    },
    goToSlide(index) {
      if (index === this.currentIndex) return

      this.userPaused = true
      this.stopAutoplay()
      this.currentIndex = index
    },
    resetCarousel() {
      this.transitionDuration = 0
      this.currentIndex = 0

      this.$nextTick(() => {
        this.updateSlideWidth()
        this.transitionDuration = 500

        if (this.autoplay && !this.userPaused) {
          this.startAutoplay()
        }
      })
    }
  }
}
</script>

<style scoped>
/* 移除max-width限制并确保容器占满父元素 */
.carousel-container {
  position: relative;
  width: 100%;
  /* height: 500px; */
  /* 高度可根据需要调整 */
  margin: 0;
  overflow: hidden;
}

.carousel-wrapper {
  display: flex;
  height: 100%;
}

.carousel-slide {
  flex-shrink: 0;
  width: 100%;
  height: 100%;
  position: relative;
  /* 保留相对定位（浮层基于此定位） */
  overflow: visible;
  /* 新增：允许浮层超出轮播项范围显示 */
}

.carousel-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  /* 保持图片比例并覆盖容器 */
}

/* 其他样式保持不变 */
.carousel-caption {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.6);
  color: white;
  padding: 15px;
  transform: translateY(100%);
  transition: transform 0.3s ease;
}

.carousel-slide:hover .carousel-caption {
  transform: translateY(0);
}

.carousel-control {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background: rgba(0, 0, 0, 0.5);
  color: white;
  border: none;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  cursor: pointer;
  z-index: 10;
}

.carousel-control.prev {
  left: 15px;
}

.carousel-control.next {
  right: 15px;
}

.carousel-indicators {
  position: absolute;
  bottom: 15px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  gap: 8px;
  z-index: 10;
}

.carousel-indicators button {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  border: none;
  background: rgba(255, 255, 255, 0.5);
  cursor: pointer;
}

.carousel-indicators button.active {
  background: white;
  width: 30px;
  border-radius: 6px;
}
</style>