<template>
  <div class="Card" @mouseenter="changeIn" @mouseleave="changeOut">
    <div class="icon" :style="{ backgroundColor: convertedColor }">
      <i class="iconStyle" :class="[
        propsClass, // 接收父组件传来的类名
      ]"></i>
    </div>
    <h3 class="title">{{ title }}</h3>
    <p class="p">{{ description }}</p>
    <a href="JavaScript:;" class="knowMore">
      <span class="more">了解更多</span>
      <i class="el-icon-right"></i>
    </a>
  </div>
</template>

<script>
export default {
  props: {
    title: {
      type: String,
      required: true,
      default: '都是我的错'
    },
    description: {
      type: String,
      required: true,
      default: '这也是一段废话，我也不知道要编什么，你将就着看吧，反正也不重要，不过还是要营造一种高端大气上档次的感觉。'
    },
    baseColor: {
      type: String,
      default: '#3B82F630'
    },
    propsClass: {
      type: String,
      default: 'el-icon-help'
    },
    iconClass: {
      type: String,
      default: 'fa-code'
    },
    iconBgClass: {
      type: String,
      default: 'bg-primary/10 text-primary'
    }
  },
  data() {
    return {
      isHover: false
    }
  },
  methods: {
    changeIn() {
      this.isHover = true
    }, changeOut() {
      this.isHover = false
    }

  },
  computed: {
    convertedColor() {
      // 核心转换逻辑：保留前6位，将最后两位替换为B0
      return this.isHover ? this.baseColor.slice(0, 7) + 'C0' : this.baseColor
    }
  }
}
</script>

<style scoped>
.Card {
  width: 250px;
  height: 300px;
  border-radius: 20px;
  padding: 30px;
  box-shadow: 2px 2px 10px 4px rgba(0, 0, 0, 0.1);
  transition: background-color 0.3s;
}

.Card:hover .icon {
  color: white;
  /* background-color: #3B82F6B0; */
}

.knowMore {
  text-decoration: none;
}

.Card:hover .more {
  text-decoration: underline;
}

.Card:hover .el-icon-right {
  transform: translateX(20px);
}

.el-icon-right {
  margin-left: 10px;
  transition: transform 0.3s ease;
}

.icon {
  width: 80px;
  height: 80px;
  /* background-color: #3B82F630;
  background-color: #f3245430; */
  position: relative;
  border-radius: 20px;
}

.iconStyle {
  font-size: 40px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.p {
  color: #1E293B90;
}
</style>