<template>
  <div class="Iconcontent" :style="{ width: iconWidth }">
    <div class="icon" :style="{
      backgroundColor: bgc
    }">
      <i class="iconStyle" :class="[
        propsClass,
      ]" :style="{
        color: txtColor
      }"></i>
    </div>
    <div class="right">
      <div class="num" :style="{
        color: numCol, fontSize: numSize
      }">{{ num }}</div>
      <div class="title" v-if="ifTitle">{{ title }}</div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    ifTitle: {
      type: [Boolean, String],
      default: true
    },
    txtCol: {
      type: String
    },
    iconWidth: {
      type: String,
      default: '200px'
    },
    numCol: {
      type: String,
      default: 'black'
    },
    numSize: {
      type: String,
      default: '18px'
    },
    propsClass: {
      type: String,
      default: 'el-icon-info'
    },
    bgc: {
      type: String,
      default: '#3B82F630'
    },
    num: {
      type: String,
      default: '200 +'
    },
    title: {
      type: String,
      default: '专业团队成员'
    }
  },
  computed: {
    txtColor() {
      return this.txtCol ? this.txtCol : this.bgc.slice(0, 7) + 'C0'
    }
  }
}
</script>

<style scoped>
.Iconcontent {
  display: flex;
  margin-bottom: 20px;
  /* width: 200px; */
}

.icon {
  width: 50px;
  height: 50px;
  position: relative;
  border-radius: 10px;
}

.iconStyle {
  font-size: 25px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.right {
  /* margin: 0 0 20px 20px; */
  margin-left: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.num {
  font-weight: bold;
  /* font-size: larger; */
}

.title {
  /* margin-top: 10px; */
  color: rgb(102, 101, 101);
}
</style>